input[type="radio"],
input[type="checkbox"] {
    position: absolute;
    left: -9999px;
}

.filter {
    background-color: color(gray-200);

    &__title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: rem(16px);
        background: darken(color(gray-200), 5%);
        @include font-weight(semibold);
        cursor: pointer;
        user-select: none;

        @include respond-to(xl) {
            background-color: transparent;
            @include font-size(text-xl);
            cursor: auto;

            i { display: none; }
        }
    }

    &__list {
        display: none;

        @include respond-to(xl) {
            display: block;
        }
    }
    
    &__item {
        text-align: center;
        
        @include respond-to(xl) {
            text-align: left;
        }

        &:not(:last-child) {
            border-bottom: 1px solid rgba(color(gray-300), .3);
        }
    }

    &__label {
        display: block;
        padding: rem(8px) rem(16px);
        cursor: pointer;
        color: color(gray-300);
        user-select: none;

        &:hover {
            color: color(secondary);
        }
    }
}

input[value="show-filter"]:checked ~ .filter__list {
    display: block;
}

input[value="cultivo"]:checked ~ .posts .posts__post:not([data-category~="cultivo"]),
input[value="agroecologia"]:checked ~ .posts .posts__post:not([data-category~="agroecologia"]),
input[value="ayuda"]:checked ~ .posts .posts__post:not([data-category~="ayuda"]),
input[value="medicina"]:checked ~ .posts .posts__post:not([data-category~="medicina"]),
input[value="odontologia"]:checked ~ .posts .posts__post:not([data-category~="odontologia"]),
input[value="fruticola"]:checked ~ .posts .posts__post:not([data-category~="fruticola"]),
input[value="hortalizas"]:checked ~ .posts .posts__post:not([data-category~="hortalizas"]),
input[value="aves"]:checked ~ .posts .posts__post:not([data-category~="aves"]),
input[value="alternativas"]:checked ~ .posts .posts__post:not([data-category~="alternativas"]) {
    display: none;
}

input[value="cultivo"]:checked ~ .filter .filter__label[for="cultivo"],
input[value="agroecologia"]:checked ~ .filter .filter__label[for="agroecologia"],
input[value="ayuda"]:checked ~ .filter .filter__label[for="ayuda"],
input[value="medicina"]:checked ~ .filter .filter__label[for="medicina"],
input[value="odontologia"]:checked ~ .filter .filter__label[for="odontologia"],
input[value="fruticola"]:checked ~ .filter .filter__label[for="fruticola"],
input[value="hortalizas"]:checked ~ .filter .filter__label[for="hortalizas"],
input[value="aves"]:checked ~ .filter .filter__label[for="aves"],
input[value="alternativas"]:checked ~ .filter .filter__label[for="alternativas"] {
    color: color(secondary);
}

// Layout sidebar
.sidebar {
    @include respond-to(xl) {
        display: grid;
        grid-template-columns: 25% 1fr;
    }

    @include respond-to(2xl) {
        grid-template-columns: 20% 1fr;
    }

    &__filter { 
        @include respond-to(xl) {
            padding: 0 rem(12px);
        }
    }

    &__posts { 
        padding: rem(16px);

        @include respond-to(2xl) {
            padding: rem(16px) rem(48px);   
        }
    }
}