.input-group {
    &__textarea,
    &__input {
        display: block;
        @include box(100%, rem(48px));
        padding: rem(16px);
        background-color: color(gray-200);
        border: rem(1px) solid color(primary);
        @include font-size(text-base, $line-height: false);
        @include font-family(body);
        color: color(secondary);

        &:focus {
            border-color: color(primary);
            outline: none;
            box-shadow: 0 0 0 1px rgba(color(primary), .99),
                        0 0 0 4px rgba(color(primary), .2);
        }
    }

    &__textarea {
        height: rem(150px);
        width: 100%;
        min-width: 100%;
        max-width: rem(200px);
        min-height: rem(130px);
    }
}