}

.text-gray-300 {
  color: #c0c0c0;
}

.text-gray-200 {
  color: #f6f6f6;
}

.text-white {
  color: #ffffff;
}

.text-black {
  color: #000000;
}

/* ------------------------------------- */
/* Color background */
/* ------------------------------------- */
.bg-primary {
  background-color: #deccb5;
}

.bg-secondary {
  background-color: #222222;
}

.bg-gray-300 {
  background-color: #c0c0c0;
}

.bg-gray-200 {
  background-color: #f6f6f6;
}

.bg-white {
  background-color: #ffffff;
}

.bg-black {
  background-color: #000000;
}

.overline span {
  display: inline-block;
  position: relative;
  padding-bottom: 0.75rem;
}

.overline span::before {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 70%;
  height: 0.1875rem;
  background-color: #deccb5;
  content: "";
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.text-justify {
  text-align: justify;
}

.lowercase {
  text-transform: lowercase;
}

.uppercase {
  text-transform: uppercase;
}

.capitalize {
