/* ------------------------------------- */
/* Header */
/* ------------------------------------- */
:root {
  --header-height: 130px;
}

.header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  background-color: #deccb5;
}

.header__wrapper {
  height: var(--header-height);
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

/* ------------------------------------- */
/* Toggle menu */
/* ------------------------------------- */
.toggle-menu {
  padding: 0.125rem;
  display: grid;
  place-items: center;
  border: 1px solid rgba(34, 34, 34, 0.3);
  border-radius: 0.25rem;
  line-height: 0;
  cursor: pointer;
  font-size: 1.875rem;
  line-height: 2.25rem;
}

@media screen and (min-width: 64rem) {
  .toggle-menu {
    display: none;
  }
}

/* ------------------------------------- */
/* Navigation */
/* ------------------------------------- */
.navigation {
  position: fixed;
  top: var(--header-height);
  left: -100%;
  z-index: 100;
  width: 100%;
  background-color: #ffffff;
}

@media screen and (min-width: 64rem) {
  .navigation {
    position: static;
    width: auto;
    background-color: transparent;
  }
}

.navigation__list {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  display: grid;
  align-items: center;
  row-gap: 1rem;
}

@media screen and (min-width: 64rem) {
  .navigation__list {
    grid-template-columns: repeat(6, auto);
    column-gap: 1rem;
    padding: 0;
  }
}

@media screen and (min-width: 80rem) {
  .navigation__list {
    column-gap: 2rem;
  }
}

.navigation__link {
  position: relative;
  display: inline-block;
  color: rgba(34, 34, 34, 0.5);
  padding-bottom: 0.125rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 600;
  text-transform: uppercase;
  transition: color 250ms ease;
}

.navigation__link:hover {
  color: #222222;
}

.navigation.show {
  left: 0;
}

.navigation__item.current .navigation__link {
  color: #222222;
}

.navigation__item.current .navigation__link::before {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50%;
  height: 0.125rem;
  background-color: #deccb5;
  content: '';
}

@media screen and (min-width: 64rem) {
  .navigation__item.current .navigation__link::before {
    background-color: #222222;
  }
}

/* ------------------------------------- */
/* Footer */
/* ------------------------------------- */
.footer {
  background-color: #222222;
  color: #ffffff;
}

.footer__wrapper {
  padding-top: 1rem;
  display: grid;
  row-gap: 1rem;
}

@media screen and (min-width: 64rem) {
  .footer__wrapper {
    grid-template-columns: repeat(4, auto);
  }
}

.footer__block-title {
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 600;
}

.footer__link {
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: #ffffff;
  transition: color 250ms ease;
}

.footer__link:hover {
  color: #deccb5;
}

.footer__social-icon {
  font-size: 1.5rem;
  line-height: 2rem;
  margin-right: 0.75rem;
}

.footer__copy {
  padding: 1rem 0;
  font-size: 0.875rem;
  line-height: 1.25rem;
  text-align: center;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

@media screen and (min-width: 64rem) {
  .footer__copy {
    grid-column: 1 / span 4;
  }
}

.gallery {
  display: grid;
  row-gap: 1rem;
}

@media screen and (min-width: 40rem) {
  .gallery {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem;
  }
}

@media screen and (min-width: 64rem) {
  .gallery {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 80rem) {
  .gallery {
    grid-template-columns: repeat(4, auto);
  }
}

.gallery__image {
  width: 100%;
  object-fit: cover;
}

@media screen and (min-width: 48rem) {
  .gallery__image {
    height: auto;
  }
}

.form-contact {
  display: grid;
  row-gap: 1rem;
  text-align: center;
}

.form-contact__loader {
  margin: auto;
}

.form-contact__response {
  font-size: 0.875rem;
  line-height: 1.25rem;
}

/* ------------------------------------- */
/* Buttons */
/* ------------------------------------- */
.button {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.5rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.09375rem;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  background-color: transparent;
  cursor: pointer;
  transition: all 200ms ease-in-out;
}

.button--primary {
  background-color: #222222;
  color: #ffffff;
  font-size: 0.875rem;
  line-height: 1.25rem;
  border: 0.125rem solid transparent;
}

.button--primary:hover {
  border-color: #222222;
  background-color: transparent;
  color: #222222;
}

.logo {
  display: inline-block;
}

.logo--header {
  width: 6.25rem;
}

.logo--footer {
  width: 12.5rem;
}

.card {
  --border-width: 5px;
  position: relative;
  text-align: center;
  background-color: #ffffff;
  padding: calc(7px + var(--border-width));
  border: 0.0625rem solid transparent;
  transition: all .5s ease;
}

.card:hover {
  border-color: rgba(0, 0, 0, 0.09);
}

.card:hover::before, .card:hover::after {
  opacity: 1;
}

.card::before, .card::after {
  content: '';
  position: absolute;
  opacity: 0;
  transition: opacity .5s ease;
}

.card::before {
  top: 0;
  left: 0;
  width: 2rem;
  height: 2rem;
  border-width: var(--border-width) 0 0 var(--border-width);
  border-style: solid;
  border-color: #222222 transparent transparent #222222;
}

.card::after {
  bottom: 0;
  right: 0;
  width: 2rem;
  height: 2rem;
  border-width: 0 var(--border-width) var(--border-width) 0;
  border-style: solid;
  border-color: transparent #222222 #222222 transparent;
}

.card__image {
  width: 100%;
  height: 15.625rem;
  object-fit: cover;
}

@media screen and (min-width: 80rem) {
  .card__image {
    height: 12.5rem;
  }
}

.card__content {
  padding-top: 0.75rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 1rem;
}

.card__title {
  font-size: 1.125rem;
  line-height: 1.75rem;
  font-weight: 600;
  color: #deccb5;
}

.banner {
  position: relative;
  height: 17.5rem;
}

@media screen and (min-width: 40rem) {
  .banner {
    height: 20rem;
  }
}

@media screen and (min-width: 48rem) {
  .banner {
    height: 23.75rem;
  }
}

@media screen and (min-width: 64rem) {
  .banner {
    height: 26.25rem;
  }
}

@media screen and (min-width: 80rem) {
  .banner {
    height: 33.75rem;
  }
}

.banner__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}

input[type="radio"],
input[type="checkbox"] {
  position: absolute;
  left: -9999px;
}

.filter {
  background-color: #f6f6f6;
}

.filter__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  background: #e9e9e9;
  font-weight: 600;
  cursor: pointer;
  user-select: none;
}

@media screen and (min-width: 80rem) {
  .filter__title {
    background-color: transparent;
    font-size: 1.25rem;
    line-height: 1.75rem;
    cursor: auto;
  }
  .filter__title i {
    display: none;
  }
}

.filter__list {
  display: none;
}

@media screen and (min-width: 80rem) {
  .filter__list {
    display: block;
  }
}

.filter__item {
  text-align: center;
}

@media screen and (min-width: 80rem) {
  .filter__item {
    text-align: left;
  }
}

.filter__item:not(:last-child) {
  border-bottom: 1px solid rgba(192, 192, 192, 0.3);
}

.filter__label {
  display: block;
  padding: 0.5rem 1rem;
  cursor: pointer;
  color: #c0c0c0;
  user-select: none;
}

.filter__label:hover {
  color: #222222;
}

input[value="show-filter"]:checked ~ .filter__list {
  display: block;
}

input[value="cultivo"]:checked ~ .posts .posts__post:not([data-category~="cultivo"]),
input[value="agroecologia"]:checked ~ .posts .posts__post:not([data-category~="agroecologia"]),
input[value="ayuda"]:checked ~ .posts .posts__post:not([data-category~="ayuda"]),
input[value="medicina"]:checked ~ .posts .posts__post:not([data-category~="medicina"]),
input[value="odontologia"]:checked ~ .posts .posts__post:not([data-category~="odontologia"]),
input[value="fruticola"]:checked ~ .posts .posts__post:not([data-category~="fruticola"]),
input[value="hortalizas"]:checked ~ .posts .posts__post:not([data-category~="hortalizas"]),
input[value="aves"]:checked ~ .posts .posts__post:not([data-category~="aves"]),
input[value="alternativas"]:checked ~ .posts .posts__post:not([data-category~="alternativas"]) {
  display: none;
}

input[value="cultivo"]:checked ~ .filter .filter__label[for="cultivo"],
input[value="agroecologia"]:checked ~ .filter .filter__label[for="agroecologia"],
input[value="ayuda"]:checked ~ .filter .filter__label[for="ayuda"],
input[value="medicina"]:checked ~ .filter .filter__label[for="medicina"],
input[value="odontologia"]:checked ~ .filter .filter__label[for="odontologia"],
input[value="fruticola"]:checked ~ .filter .filter__label[for="fruticola"],
input[value="hortalizas"]:checked ~ .filter .filter__label[for="hortalizas"],
input[value="aves"]:checked ~ .filter .filter__label[for="aves"],
input[value="alternativas"]:checked ~ .filter .filter__label[for="alternativas"] {
  color: #222222;
}

