.philosophy {
    &__bg-image {
        @include background-cover;
        background-image: url(../img/nosotros/overlay.webp);
    }

    &__image {
        @include respond-to(xl) {
            transform: scaleX(1.15) scaleY(1.15);
            box-shadow:
                0 -4.1px 2.2px rgba(color(black), 0.02),
                0 -3.8px 5.3px rgba(color(black), 0.028),
                0 2.2px 10px rgba(color(black), 0.035),
                0 15.7px 17.9px rgba(color(black), 0.042),
                0 41.2px 33.4px rgba(color(black), 0.05),
                0 50px 50px rgba(color(black), 0.07)
            ;
        }
    }
}