.list {
    li::marker {
        color: color(secondary);
    }

    &--accent {
        li::marker {
            color: color(primary);
        }
    }
}

ul.list {
    li {
        list-style-type: disc;
        list-style-position: inside;
    }
}

ol.list {
    li {
        list-style-type: decimal;
        list-style-position: inside;
    }
}