.card {
    --border-width: 5px; 
    position: relative;
    text-align: center;
    background-color: color(white);
    padding: calc(7px + var(--border-width));
    border: rem(1px) solid transparent;
    transition: all .5s ease;

    &:hover {
        border-color: rgba(color(black), .09);

        &::before,
        &::after {
            opacity: 1;
        }
    }

    &::before,
    &::after {
        content: '';
        position: absolute;
        opacity: 0;
        transition: opacity .5s ease;
    }

    &::before {
        top: 0;
        left: 0;
        @include box(rem(32px));
        border-width: var(--border-width) 0 0 var(--border-width);
        border-style: solid;
        border-color: color(secondary) transparent transparent color(secondary);
    }
    
    &::after {
        bottom: 0;
        right: 0;
        @include box(rem(32px));
        border-width: 0 var(--border-width) var(--border-width) 0;
        border-style: solid;
        border-color: transparent color(secondary) color(secondary) transparent;
    }

    &__image {
        width: 100%;
        height: rem(250px);
        object-fit: cover;

        @include respond-to(xl) {
            height: rem(200px);
        }
    }

    &__content {
        padding-top: rem(12px);
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: rem(16px);
    }

    &__title {
        @include font-size(text-lg);
        @include font-weight(semibold);
        color: color(primary);
    }
}