// Decoracin de los titulos
.overline {
    span {
        display: inline-block;
        position: relative;
        padding-bottom: rem(12px);

        &::before {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 70%;
            height: rem(3px);
            background-color: color(primary);
            content: "";
        }
    }
}