.banner {
    position: relative;
    height: rem(280px);
    
    @include respond-to(sm) {
        height: rem(320px);
    }
    
    @include respond-to(md) {
        height: rem(380px);
    }
    
    @include respond-to(lg) {
        height: rem(420px);
    }
    
    @include respond-to(xl) {
        height: rem(540px);
    }

    &__image {
        position: absolute;
        top: 0;
        left: 0;
        @include box(100%);
        object-fit: cover;
        object-position: top;
    }
}