.gallery {
    display: grid;
    row-gap: rem(16px);

    @include respond-to(sm) {
        grid-template-columns: repeat(2, 1fr);
        column-gap: rem(16px);
    }

    @include respond-to(lg) {
        grid-template-columns: repeat(3, 1fr);
    }
    
    @include respond-to(xl) {
        grid-template-columns: repeat(4, auto);
    }
    
    &__image {
        width: 100%;
        object-fit: cover;

        @include respond-to(md) {
            height: auto;
        }
    }
}