/* ------------------------------------- */
/* Header */
/* ------------------------------------- */
:root {
    --header-height: 130px;
}

.header {
    position: sticky;
    top: 0;
    left: 0;
    @include z-index(z-100);
    width: 100%;
    background-color: color(primary);

    &__wrapper {
        height: var(--header-height);
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
    }
}

/* ------------------------------------- */
/* Toggle menu */
/* ------------------------------------- */
.toggle-menu {
    padding: rem(2px);
    display: grid;
    place-items: center;
    border: 1px solid rgba(color(secondary), .3);
    border-radius: rem(4px);
    line-height: 0;
    cursor: pointer;
    @include font-size(text-3xl);

    @include respond-to(lg) {
        display: none;
    }
}